  * {
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
    font-family: Poppins, Helvetica, Arial, Sans-Serif, serif;
    scroll-behavior: smooth;
  }

  html,
  h1,
  body,
  h2,
  h3,
  h4,
  th,
  td,
  tr,
  h5,
  h6,
  table,
  tr,
  td,
  th,
  button,
  p,
  a,
  li,
  img {
    margin: 0;
    padding: 0;
  }

  body {
    background-color: #fff;
  }

  header > div:first-of-type {
    padding: 6px 0;
    margin: 0 auto;
    word-wrap: break-word;
  }

  main span {
    font-size: 35px;
    font-weight: 400;
    display: block;
    letter-spacing: 5px;
    text-transform: uppercase;
    text-align: center;
  }

  header > div:nth-of-type(2) {
    padding-top: 40px;
  }

  .main_image {
    float: right;
    width: 50%;
  }

  h1 {
    font-size: 3em;
    font-weight: 800;
    margin-top: 20px;
    text-align: center;
    position: relative;
    z-index: 100;
  }

  main {
    display: flex;
    justify-content: center;
    padding: 0 0 40px ;
    margin: 0 auto;
    background: black;
  }

  .content {
    border: 2px solid #EBBE7D;
    border-radius: 0 0 25px 25px;
    padding: 10px 30px;
    width: 1200px;
    border-top: none;
    background-color: #131313;
  }

  .front {
    width: 100%;
    background-color: black;
  }

  .banner {
    height: 100%;
    position: relative;
    top: 4px;
    left: 50%;
    transform: translateX(-50%);
  }

  p, ul, ol, li {
    font-size: 16px;
    font-weight: 300;
    margin: clamp(1rem,.5em,2.5rem) auto clamp(1rem,.25em,2rem);
    line-height: 1.6;
    text-transform: none;
    color: black;
  }

  .center {
    text-align: center;
  }

  main table {
    display: block;
    margin: 15px auto;
    border: 1px solid #EBBE7D;
    border-radius: 20px;
    border-collapse: collapse;
    width: fit-content;
    overflow: auto;
  }

  main tbody {
    width: 100%;
  }

  main table td {
    padding: 10px;
    vertical-align: middle;
  }

  main table tr:not(:last-of-type) td {
    border-bottom: 1px solid #EBBE7D;
  }

  main table tr td {
    border-left: 1px solid #EBBE7D;
  }

  .table_container {
    overflow-x: auto;
  }

  h2, h3 {
    font-family: Mulish,Helvetica,Arial,Sans-Serif,serif;
    font-weight: 700;
    font-size: 2.2em;
    line-height: 1.143;
    margin: clamp(1rem,.5em,2.5rem) auto clamp(1rem,.25em,2rem);
  }


  .faq > div {
    min-width: 15em;
    position: relative;
    margin-top: 16px;
    width: calc(100% - 215px);
    margin-left: 99px;
  }

  .select {
    background: #117989;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px #117989 solid;
    border-radius: 0.5em;
    padding: 12px 16px;
    cursor: pointer;
  }

  .select > span {
    font-size: 18px;
    text-align: start;
    text-transform: none;
    letter-spacing: 1px;
    font-weight: 500;
  }

  .select-clicked {
    border: 1px #FFD60E solid;
    border-bottom: none;
  }

  .dropdown__element > div > p {
    margin: 0;
  }

  .menu {
    list-style: none;
    background: #117989;
    border: 1px #117989 solid;
    box-shadow: 0 0.5em 1em rgba(0, 0, 0, 0.2);
    border-radius: 0.5em;
    color: #9fa5b5;
    top: 3em;
    width: 100%;
    opacity: 0;
    display: none;
    transition: 0.2s;
    z-index: 1;
    margin-top: 16px;
  }

  .menu li {
    padding: 0px 16px;
    border-radius: 0.5em;
    cursor: pointer;
    font-size: 15px;
    display: flex;
    gap: 10px;
    align-items: center;
  }

  .menu-open {
    display: block;
    opacity: 0.75;
    padding-left: 0;
    background-color: #117989;
  }

  .dropdown__element {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  .dropdown--last {
    margin-bottom: 93px;
  }


  footer > p {
    color: #fff;
    text-align: center;
  }

  .list_titles {
    margin: 0;
  }

  .list_titles > li {
    margin: 0;
  }

  .tables {
    display: flex;
    gap: 30px;
    justify-content: center;
  }

  .header_container {
    position: relative;
    justify-content: center;
    z-index: 233;
    align-items: center;
    display: flex;
    gap: 30px;
  }

  .list_titles > li > a {
    color: #fff;
    font-weight: 600;
    text-decoration: none;
  }

  header > div:first-of-type > div {
    margin: 0 auto;
    width: 90%;
    justify-content: space-between;
    display: flex;
    align-items: center;
  }

  header > div:first-of-type {
    background-color: #131313;
    ;
  }

  .buttons {
    display: flex;
    gap: 20px;
  }

  .buttons > a {
    background: rgba(235, 190, 125, 0.6);
    padding: 10px 20px;
    color: #75DB61;
    text-decoration: none;
    border-radius: 7px;
    z-index: 999;
  }

