    width: 80%;
    margin: 20px 0;
    position: relative;
    left: 50%;
    border-radius: 20px;
    transform: translateX(-50%);
  }

  .images_footer img {
    height: 30px; 
  }

  .box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .big_footer_images {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
  }

  .big_footer_images img {
    height: 30px;
  }

