  footer {
    background-color: #131313;
    padding: 20px 35px;
  }

  footer > p {
    color: #fff;
    text-align: center;
  }

  .image_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
  }

  .image__container {
    display: flex;
    gap: 20px ;
