  }

  .table_image_container {
    display: flex;
    flex-direction: column;
    gap: 7px;
    align-items: center;
  }

  .image_container, .check_container {
    display: flex;
    gap: 7px;
    justify-content: start;
    align-items: center;
  }

  .list > li {
    width: calc(33% - 15px);
    margin: 0;
  }

  .adres_container {
    margin-top: 20px;
  }

  .adres {
    width: 450px;
    border-radius: 20px;
  }

  .adresi, .bahis {
    border-radius: 20px;
    width: 40%;
  }

  .oyun, .vpn {
    width: 33%;
    border-radius: 20px;
  }

  .adresi_container {
    flex-direction: row-reverse;
  }

  @media (max-width: 1200px) {
    main {
      padding: 40px 50px;
    }
  }

  @media (max-width: 1024px) {
    .adres_container, .bahis_container, .oyun_container {
      flex-direction: column;
    }

    .license_container {
      flex-direction: column;
    }

    .options_container {
      flex-direction: column-reverse;
    }

    .destek_container {
      max-width: 100%;
    }

    .mobile {
      width: 250px;
    }

    .license, .bahis, .oyun {
      width: 100%;
    }

    .license {
      max-width: 300px;
    }

  }

  @media (max-width: 768px) {
    .homepage_container, .text_container {
      flex-direction: column;
    }

    .text_container > p {
      width: 100%;
    }

    .list > li {
      width: calc(50% - 15px);
      margin: 0;
    }

    .options {
      width: 250px;
    }

    .homepage {
      width: 100%;
    }
  }

  @media (max-width: 720px) {
    .center_image {
      width: 100%;
    }

    .image__container {
      flex-direction: column;
        }
  
    .app {
      width: 100%;
    }

    .adres, .adresi, .vpn {
      width: 100%;
    }

    ul, ol {
      padding-left: 10px;
    }

    main {
      padding: 30px 20px;
    }

    .image_container {
      flex-direction: column;
    }

    header > div:first-of-type > div {
      flex-direction: column;
      gap: 20px;
    }

    h1 {
      font-size: 2em;
    }

    .options_container {
      flex-direction: column-reverse;
    }
  }

  .toc-gen__show {
    content: '+';
    color: white;
    margin-right: 15px;
    cursor: pointer;
    background: 0 0;
    border: none;
    font-size: 1rem;
    text-transform: capitalize;
  }

  .toc-gen__list {
    opacity: 0;
    position: absolute;
  }

  .toc-gen__list_showed {
    position: relative;
    opacity: 1;
    pointer-events: auto;
  }

  footer .content {
    max-width: 1350px;
    margin: auto;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    border: none;
    background-color: #131313;
  }

  footer .content p,
  a {
    color: #fff
  }

  footer .content .box {
    transition: all .4s ease
  }

  footer .content .topic {
    font-size: 22px;
    font-weight: 600;
    color: #fff;
    margin-bottom: 16px
  }

  footer .content p {
    text-align: justify
  }

  footer .content .lower .topic {
    margin: 24px 0 5px
  }

  footer .content .lower i {
    padding-right: 16px
  }

  footer .content .middle {
    padding-left: 80px
  }

  footer .content .middle a {
    line-height: 32px
  }

  footer .content .right input[type=text] {
    height: 45px;
    width: 100%;
    outline: none;
    color: #d9d9d9;
    background: #000;
    border-radius: 5px;
    padding-left: 10px;
    font-size: 17px;
    border: 2px solid #222
  }

  footer .content .right input[type=submit] {
    height: 42px;
    width: 100%;
    font-size: 18px;
    color: #d9d9d9;
    background: #eb2f06;
    outline: none;
    border-radius: 5px;
    letter-spacing: 1px;
    cursor: pointer;
    margin-top: 12px;
    border: 2px solid #eb2f06;
    transition: all .3s ease-in-out
  }

  .content .right input[type=submit]:hover {
    background: 0 0;
    color: #eb2f06
  }

  footer .content .media-icons a {
    font-size: 16px;
    height: 45px;
    width: 45px;
    display: inline-block;
    text-align: center;
    line-height: 43px;
    border-radius: 5px;
    border: 2px solid #222;
    margin: 30px 5px 0 0;
    transition: all .3s ease
  }

  .content .media-icons a:hover {
    border-color: #eb2f06
  }

  footer .bottom {
    width: 100%;
    text-align: right;
    color: #d9d9d9;
    padding: 0 40px 5px 0
  }

  footer .bottom a {
    color: #eb2f06
  }

  footer a {
    transition: all .3s ease
  }

  footer a:hover {
    color: #eb2f06
  }

  table p, table h3 {
    margin: 0;
  }

  @media(max-width:1100px) {
    footer .content .middle {
        padding-left: 50px
    }
  }

  @media(max-width:950px) {
    footer .content .box {
        width: 50%
    }
    .content .right {
        margin-top: 40px
    }
  }

  @media(max-width:560px) {
    footer {
        position: relative
    }
    footer .content .box {
        width: 100%;
        margin-top: 30px
    }
    footer .content .middle {
        padding-left: 0
    }
  }
